"use client"

import { useEffect, useRef } from "react"

export default function CircleScrollSection() {
  const mainTextRef = useRef<HTMLDivElement>(null)
  const secondaryTextRef = useRef<HTMLDivElement>(null)
  const leftImgRef = useRef<HTMLDivElement>(null)
  const rightImgRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    let lastScrollY = window.scrollY

    const handleScroll = () => {
      const scrollTop = window.scrollY

      // MAIN CIRCLE ROTATION
      if (mainTextRef.current) {
        mainTextRef.current.style.transform =
          `translate(-50%, -50%) rotate(${scrollTop * 0.12}deg)`
      }

      // OUTER SECONDARY ROTATION
      if (secondaryTextRef.current) {
        secondaryTextRef.current.style.transform =
          `translate(-50%, -50%) rotate(${scrollTop * -0.08}deg)`
      }

      // LEFT IMAGE PARALLAX
      if (leftImgRef.current) {
        leftImgRef.current.style.transform =
          `translateY(${scrollTop * 0.15}px)`
      }

      // RIGHT IMAGE PARALLAX
      if (rightImgRef.current) {
        rightImgRef.current.style.transform =
          `translateY(${scrollTop * -0.15}px)`
      }

      lastScrollY = scrollTop
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <section className="relative w-full min-h-screen flex items-center justify-center bg-[#111] overflow-hidden py-32">

      {/* LEFT PARALLAX IMAGE */}
      <div
        ref={leftImgRef}
        className="absolute left-10 top-1/2 -translate-y-1/2 hidden md:block"
      >
        <img
          src="/circle_l-500x500.jpg"
          alt="Left circle"
          className="w-[280px] h-[280px] rounded-full object-cover opacity-90"
        />
      </div>

      {/* RIGHT PARALLAX IMAGE */}
      <div
        ref={rightImgRef}
        className="absolute right-10 top-1/2 -translate-y-1/2 hidden md:block"
      >
        <img
          src="/circle_r.jpg"
          alt="Right circle"
          className="w-[280px] h-[280px] rounded-full object-cover opacity-90"
        />
      </div>

      {/* MAIN CENTER CIRCLE + ROTATING TEXT */}
      <div className="relative flex items-center justify-center">

        {/* CENTER IMAGE */}
        <div className="w-[420px] h-[420px] md:w-[620px] md:h-[620px] rounded-full overflow-hidden shadow-[0_0_50px_rgba(0,0,0,0.3)]">
          <img
            src="/circle_img.jpg"
            alt="Center image"
            className="w-full h-full object-cover"
          />
        </div>

        {/* MAIN ROTATING TEXT */}
        <div
          ref={mainTextRef}
          className="absolute inset-0 flex items-center justify-center pointer-events-none"
        >
          <svg viewBox="0 0 1150 1150" className="w-[850px] h-[850px]">
            <defs>
              <path
                id="mainPath"
                d="M 575 185 A 390 390 0 0 1 575 965 A 390 390 0 0 1 575 185"
              />
            </defs>

            <text fontSize="90" fill="#E7D9CD" fontWeight="bold">
              <textPath href="#mainPath" startOffset="0%">
                HAIRSTYLE MASTERS
              </textPath>
            </text>

            <text fontSize="90" fill="#E7D9CD" fontWeight="bold">
              <textPath href="#mainPath" startOffset="50%">
                HAIRSTYLE MASTERS
              </textPath>
            </text>
          </svg>
        </div>

        {/* OUTER ROTATING TEXT */}
        <div
          ref={secondaryTextRef}
          className="absolute inset-0 flex items-center justify-center pointer-events-none"
        >
          <svg viewBox="0 0 1150 1150" className="w-[1150px] h-[1150px] opacity-40">
            <defs>
              <path
                id="secondaryPath"
                d="M 575 45 A 530 530 0 0 1 575 1105 A 530 530 0 0 1 575 45"
              />
            </defs>

            <text fontSize="52" fill="#ffffff" fontWeight="bold">
              <textPath href="#secondaryPath" startOffset="0%">
                We combine traditional skills with modern innovations to deliver high quality
                services for every client
              </textPath>
            </text>
          </svg>
        </div>

      </div>

    </section>
  )
}
